function getWhitelist(cb) {
  chrome.storage.local.get('whitelist', function(d) {
    var ret = {};
    if (!d.whitelist || d.whitelist.constructor.name != 'Array') {
      cb(ret);
      return;
    }
    $.each(d.whitelist, function(index, value) {
      ret[value] = true;
    })
    cb(ret);
  })
}

function saveWhitelist(whitelist, cb) {
  chrome.storage.local.set({
    'whitelist': Object.keys(whitelist)
  }, cb)
}

function addToWhitelist(email, cb) {
  getWhitelist(function(whitelist) {
    whitelist[email] = true;
    saveWhitelist(whitelist, cb);
  })
}

function clearWhitelist() {
  chrome.storage.local.remove('whitelist');
}

function isWhitelisted(email, cb) {
  chrome.storage.local.get(['whitelist', 'serverMode'], function(d) {
    if (d.serverMode == 1) {
      cb(true);
      return;
    }
    if (d.serverMode != 2) {
      var ret = {};
      if (!d.whitelist || d.whitelist.constructor.name != 'Array') {
        cb(false, true);
        return;
      }
      cb(d.whitelist.indexOf(email) != -1, true);
      return;
    }

    getAuthToken(false, function(token) {
      if (!token) {
        console.error('unable to get token for vysor enterprise whitelist check');
        cb(false);
        return;
      }

      $.ajax({
        type: 'get',
        url: 'https://billing.vysor.io/whitelist',
        headers: {
          'Authorization': 'Bearer ' + token
        },
        data: {
          email: email
        },
        error: function() {
          console.error('failure checking vysor enterprise whitelist', arguments)
          cb(false);
        },
        success: function(data) {
          if (!data.whitelist)
            console.error('access denied to', email, data);
          cb(data.whitelist, false);
        }
      });
    })
  })
}

function getVysorWhitelistStatus(cb) {
  chrome.storage.local.get(['whitelist', 'serverMode'], function(d) {
    if (d.serverMode == 1) {
      cb('Any user can access this server.');
      return;
    }
    if (d.serverMode == 2) {
      cb('Your Vysor Enterprise users can access this server.');
      return;
    }
    var number;
    if (!d.whitelist || d.whitelist.constructor.name != 'Array')
      number = 0;
    else
      number = d.whitelist.length;
    cb(number + ' user(s) can access this server.');
  })
}

exports.getVysorWhitelistStatus = getVysorWhitelistStatus;
exports.isWhitelisted = isWhitelisted;
exports.clearWhitelist = clearWhitelist;
exports.addToWhitelist = addToWhitelist;
exports.saveWhitelist = saveWhitelist;
exports.getWhitelist = getWhitelist;
